:root {
  --primaryDkBlue: hsl(208, 100%, 34%);
  --primaryLtBlue: hsl(200, 89%, 85%);
  --primaryYellow: hsl(49, 100%, 50%);
  --primaryYellow_60: hsl(49, 100%, 50%,0.6);
  --primaryDkYellow: hsl(60, 100%, 38%);
  --primaryDkGreen: hsl(124, 45%, 47%);
  --primaryGreen: hsl(92, 51%, 51%);
  --primaryOrange: hsl(32, 94%, 60%);
  --primaryDkOrange: hsl(23, 73%, 43%);
  --primaryRed: hsl(347, 100%, 45%);
  --weBlue: hsl(196, 100%, 47%);
  --darkBlue: hsl(218, 61%, 31%);
  --black: hsl(0, 0%, 0%);
  --textGray: hsl(0, 0%, 44%);
  --footerGray: hsl(0, 0%, 81%);
  --slateGray: hsl(210, 13%, 50%);
  --dkSlateGray: hsl(180, 25%, 25%);
  --dkGray: hsl(0, 0%, 41%);
  --ltGray: hsl(0, 0%, 83%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-text: "Lato", Helvetica, Arial, sans-serif;
  --font-heading: "Georama", Helvetica, Arial, sans-serif: // heading font ;
}

@keyframes no-transform {
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes pulse {
  0% {
    opacity: 1;
    transform: none;
  }
  50% {
    opacity: 0.8;
    transform: scale(0.8);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 300ms ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 300ms ease-in-out forwards;
  display: none;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
}

body {
  font-family: var(--font-text);
  line-height: 1.4;
  background-color: var(--white);
  margin: 0;
  padding: 0;
}

.container {
  width: 80%;
  width: min(90%, 75em);
  margin-inline: auto;
}
.container__content {
  width: 60%;
  margin-bottom: 2.5rem;
}
@media (max-width: 59.999375em) {
  .container__content {
    width: 80%;
  }
}
@media (max-width: 43.74875em) {
  .container__content {
    width: 100%;
  }
}

.row {
  padding: 0 1.25rem;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-sa {
  justify-content: space-around;
}
.flex-jc-c {
  justify-content: center;
}
.flex-jc-fs {
  justify-content: flex-start;
}
.flex-jc-fe {
  justify-content: flex-end;
}
.flex-ai-fs {
  align-items: flex-start;
}
.flex-ai-c {
  align-items: center;
}
.flex-fd-r {
  flex-direction: row;
}
.flex-fd-c {
  flex-direction: column;
}
.flex-fw-w {
  flex-wrap: wrap;
}
.flex-fw-nw {
  flex-wrap: nowrap;
}

@media (max-width: 59.999375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 60em) {
  .hide-for-desktop {
    display: none;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-family: var(--font-heading);
  font-weight: 700;
  line-height: 1.1;
}

h1 {
  font-size: 4.6875rem;
}

h2 {
  font-size: 3.625rem;
}

h3 {
  font-size: 2.75rem;
}

h4 {
  font-size: 2.125rem;
}

h5 {
  font-size: 1.75rem;
}

h6 {
  font-size: 1.375rem;
}

p {
  font-family: var(--font-text);
  color: var(--textGray);
  font-size: 1rem;
  font-weight: 500;
  line-height: 1.5;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active,
a:hover {
  text-decoration: none;
}

.button {
  text-align: center;
  display: inline-block;
  cursor: pointer;
  font-size: 1.125rem;
}

.btn {
  font-family: var(--font-heading);
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.125rem;
  padding: 0.625rem 1.25rem;
  border-radius: 0.3125rem;
  cursor: pointer;
  letter-spacing: 0.0625rem;
}
.btn a {
  color: white;
}
.btn a:hover {
  color: var(--primaryDkBlue);
  transition: all 300ms ease-in-out;
}
.btn--lc {
  text-transform: lowercase;
  font-weight: 300;
}
.btn--white {
  background-color: var(--white);
  border: 1px solid var(--white);
  color: var(--primaryDkBlue);
  font-weight: 700;
}
.btn--white:hover, .btn--white:active, .btn--white:focus {
  background-color: var(--primaryDkBlue);
  color: var(--white);
  border: 0.0625rem solid var(--white);
  transition: all 300ms ease-in-out;
}
.btn--dark {
  background-color: var(--primaryDkBlue);
  border: 0.0625rem solid var(--primaryDkBlue);
  color: var(--white);
  font-weight: 700;
}
.btn--dark:hover, .btn--dark:active, .btn--dark:focus {
  background-color: var(--white);
  color: var(--primaryDkBlue);
  border: 0.0625rem solid var(--primaryDkBlue);
  transition: all 300ms ease-in-out;
}
.btn--dark-footer {
  font-size: 1.5rem;
  font-weight: 500;
  padding: 0.625rem 3.5rem;
  text-transform: uppercase;
  letter-spacing: 0.0625rem;
  display: block;
}

footer {
  min-height: 19.6875rem;
  background-color: var(--footerGray);
}

header {
  background-color: var(--primaryYellow);
  width: 100%;
  position: absolute;
  z-index: 900;
}
header__logo img {
  padding: 0.625rem 0;
}
header.open .header__menu > span:first-child {
  transform: rotate(45deg);
}
header.open .header__menu > span:nth-child(2) {
  opacity: 0;
}
header.open .header__menu > span:last-child {
  transform: rotate(-45deg);
}
header .overlay {
  opacity: 0;
  position: fixed;
  top: 3.75rem;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--primaryYellow);
  z-index: 400;
  display: none;
}

.nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 400;
  padding: 0.625rem 0 0.3125rem;
}
@media (max-width: 59.999375em) {
  .nav {
    padding: 1rem 0;
  }
}
.nav__list {
  display: flex;
  align-items: center;
  list-style-type: none;
  padding: 0;
  margin: 0;
}
@media (max-width: 59.999375em) {
  .nav__list {
    display: none;
  }
}
.nav__list--open {
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 100%;
  padding-left: 0;
  transition: all 300ms ease-in-out;
}
.nav__toggle {
  width: 2.25em;
  margin-right: 0.625em;
  position: relative;
  cursor: pointer;
}
.nav__toggle > span {
  display: block;
  width: 1.625em;
  height: 0.125em;
  background-color: var(--primaryDkBlue);
  transition: all 300ms ease-in-out;
  transform-origin: 0.1875rem 0.0625rem;
}
.nav__toggle > span:not(:last-child) {
  margin-bottom: 0.3125em;
}
.nav__item a {
  font-family: var(--font-heading);
  font-size: 1.125rem;
  color: var(--primaryDkBlue);
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 0.0625rem;
  cursor: pointer;
  position: relative;
  margin-left: 1.875em;
  transition: color 0.2s ease-out;
}
@media (max-width: 59.999375em) {
  .nav__item a {
    margin-left: 0px;
  }
}
.nav__item a::after {
  border-radius: 0.0625em;
  border-top: 0.1875em solid var(--white);
  content: "";
  position: absolute;
  right: 100%;
  bottom: -em(8);
  left: 0;
  transition: right 0.4s cubic-bezier(0, 0.5, 0, 1), border-color 0.4s ease-out;
}
.nav__item a:hover {
  color: var(--white);
  text-decoration: none;
}
.nav__item a:hover::after {
  animation: anchor-underline 2s cubic-bezier(0, 0.5, 0, 1) infinite;
  border-color: var(--white);
}
.nav__item--lc {
  font-weight: 400;
  text-transform: none;
}
.nav__item--btn {
  margin-left: 2.1875rem;
}
@media (max-width: 59.999375em) {
  .nav__item--btn {
    margin-left: 0px;
  }
}

.nav-open {
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 4.6875em;
  text-align: center;
  width: 90%;
  padding-top: 1.25em;
  padding-left: 0;
  padding-bottom: 1.25em;
  z-index: 400;
}

.chevron-down {
  font-size: 1.125rem;
  font-weight: 700;
}

li.nav__item:first-child a {
  margin-left: 0;
}

.hero__index {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  overflow: hidden;
  padding: 0;
  margin-bottom: 2.5rem;
}
@media (max-width: 59.999375em) {
  .hero__index {
    transition: all 350ms ease-in-out;
    margin-bottom: 2.5rem;
  }
}
.index__bulkOrder {
  width: 60%;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 2.5rem;
}
@media (max-width: 59.999375em) {
  .index__bulkOrder {
    width: 90%;
  }
}

.bulk__header {
  font-size: 2.75rem;
  color: var(--primaryDkBlue);
  margin-bottom: 1.5625rem;
}
.bulk__intro {
  font-family: var(--font-text);
  color: var(--textGray);
  font-size: 1.375rem;
  font-weight: 500;
}
.bulk__intro p {
  margin-bottom: 0;
}

.index__fullWidth {
  padding: 2.5rem 0;
  text-align: center;
}
.index__fullWidth--text {
  width: 45%;
}
@media (max-width: 43.74875em) {
  .index__fullWidth--text {
    width: 100%;
  }
}
.index__fullWidth--solutions {
  width: 45%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 53%;
  text-align: center;
  z-index: 900;
}
@media (max-width: 59.999375em) {
  .index__fullWidth--solutions {
    width: 100%;
  }
}
@media (max-width: 43.74875em) {
  .index__fullWidth--solutions {
    top: 40%;
  }
}
.index__fullWidth--head, .index__fullWidth--subHead, .index__fullWidth--subText {
  color: var(--white);
}
.index__fullWidth--head {
  margin-bottom: 0.3125rem;
}
.index__fullWidth--subHead {
  line-height: 1.4;
  margin-bottom: 0.625rem;
}
.index__fullWidth--subText {
  width: 65%;
  margin: 0 auto;
  margin-bottom: 1.875rem;
}

.indexProducts,
.indexSolutions {
  background-position: center;
  background-size: cover;
  margin-bottom: 2.5rem;
  margin-left: auto;
}

.indexProducts {
  background: url("/img/index_products_1920x700-min.jpg") no-repeat;
  background-position: top;
  background-size: cover;
}
@media (max-width: 43.74875em) {
  .indexProducts {
    background-color: var(--primaryDkBlue);
    height: auto;
  }
}

.indexSolutions {
  background: url("/img/index_solutions_1920x775-min.jpg") no-repeat;
  background-position: top;
  background-size: cover;
  padding-top: 17.1875rem;
  margin-bottom: 3.75rem;
  color: var(--darkBlue);
  position: relative;
}
.indexSolutions__background {
  position: relative;
  height: 18.75rem;
  background-color: var(--primaryYellow_60);
}
@media (max-width: 43.74875em) {
  .indexSolutions__background {
    height: 23.4375rem;
  }
}
@media (max-width: 43.74875em) {
  .indexSolutions {
    background-color: var(--primaryYellow);
    padding-top: 12.5rem;
  }
}
.indexSolutions__head {
  margin-bottom: 0.3125rem;
}
.indexSolutions__subHead {
  line-height: 1;
  margin-bottom: 0.9375rem;
}
@media (max-width: 43.74875em) {
  .indexSolutions__subHead {
    width: 90%;
    margin: 0 auto 0.625rem;
  }
}
.indexSolutions__subText {
  margin: 0 auto 1.875rem;
  width: 65%;
}
@media (max-width: 43.74875em) {
  .indexSolutions__subText {
    width: 90%;
  }
}

.product__cards {
  margin: 4.6875rem auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1rem;
}
@media (max-width: 59.999375em) {
  .product__cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
.product__card {
  padding: 1.25rem 0 0.625rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0.625rem 0.9375rem 0.625rem 1.5625rem;
  border: 1px solid var(--primaryDkBlue);
  height: 100%;
  transition: background-color 300ms ease-in-out, border 300ms ease-in-out;
}
@media (max-width: 43.74875em) {
  .product__card {
    display: block;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
}
.product__card:hover {
  background-color: var(--primaryLtBlue);
  border: 1px solid var(--primaryLtBlue);
}
.product__card--content {
  display: flex;
  align-items: center;
}
@media (max-width: 43.74875em) {
  .product__card--content {
    display: block;
    text-align: center;
  }
}
.product__card--content p {
  font-size: 1.125rem;
  line-height: 1.2;
  font-weight: 700;
  color: var(--primaryDkBlue);
  margin-bottom: 0;
}
.product__card--image {
  max-width: 100%;
  height: auto;
  margin-right: 0.9375rem;
}
@media (max-width: 43.74875em) {
  .product__card--image {
    margin-right: 0;
  }
}

.product__card9 {
  grid-column-start: 2;
  grid-column-end: 3;
}
@media (max-width: 59.999375em) {
  .product__card9 {
    grid-column-start: inherit;
    grid-column-end: inherit;
  }
}

.product__card10 {
  grid-column-start: 3;
  grid-column-end: 4;
}
@media (max-width: 59.999375em) {
  .product__card10 {
    grid-column-start: inherit;
    grid-column-end: inherit;
  }
}